import React, {
  ComponentType,
  ElementType,
  useCallback,
  useEffect,
  useRef,
  useState,
} from "react";
export interface SliderProps<T> {
  CONTAINER_WIDTH: number;
  ITEMS: T[];
  action: (type: SLIDER_TYPE) => void;
  listRef: React.RefObject<HTMLDivElement>;
  childRef: React.RefObject<HTMLDivElement>;
  children: ElementType;
  title: string;
}
type SLIDER_TYPE = "LEFT" | "RIGHT";
const withSlider = <T,>(WrappedComponent: ComponentType<SliderProps<T>>) => {
  return (
    props: Omit<SliderProps<T>, "action" | "listRef" | "childRef">
    // Omit: To ra 1 thc th mi da vo thc th c, v s khng bao gm cc thuc tnh/phng thc c ch nh trong Omit
  ) => {
    const {CONTAINER_WIDTH, ITEMS, children } = props;
    const listRef = useRef<HTMLDivElement>(null);
    const childRef = useRef<HTMLDivElement>(null); // To ref
    const [currentIndex, setCurrentIndex] = useState(0); // V tr hin ti ca slider
    const [width, setWidth] = useState(0);
    const MAX_VISIBLE_ITEMS = Math.floor(CONTAINER_WIDTH / width); // S lng item ti a c th i c
    const TOTAL_ITEMS = ITEMS.length; // Tng s item

    useEffect(() => {
      if (childRef.current) {
        setWidth(childRef.current.clientWidth + 5);
      }
    }, []);

    // Khi nhn th index tng ln 1, ly Index  nhn cho tng chiu di ca box, ri thc hin translate
    const handleClick = useCallback(
      (type: SLIDER_TYPE) => {
        if (!listRef.current) return;

        let newIndex = currentIndex;
        if (type === "LEFT" && currentIndex > 0) {
          newIndex -= 1; // Li li
        } else if (
          type === "RIGHT" &&
          currentIndex < TOTAL_ITEMS - MAX_VISIBLE_ITEMS
        ) {
          newIndex += 1; // Tin ln
        } else {
          return;
        }

        setCurrentIndex(newIndex); // Cp nht v tr mi
        listRef.current.style.transform = `translateX(${-newIndex * width}px)`;
      },
      [currentIndex, width, MAX_VISIBLE_ITEMS, TOTAL_ITEMS]
    );

    return (
      <WrappedComponent
        childRef={childRef}
        {...props}
        action={handleClick}
        listRef={listRef}
      >
        {children}
      </WrappedComponent>
    );
  };
};

export default withSlider;
